body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #e6e7ed;
  display: -webkit-flex;
  display: flex;
  font-family: 'Helvetica', 'Arial', sans-serif;
  font-weight: bold;
}

.App {
  display: -webkit-flex;
  display: flex;
  width: 100vw;
  height: 100vh;
}

.map-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.current-wrapper {
  height: 0;
  width: 100%;

  position: relative;
  bottom: 15vh;
  font-size: 32px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.current-code {
  margin: auto;
  cursor: pointer;
}

.note {
  font-size: 16px;
  margin: auto;
  cursor: pointer;
}



@media only screen and (max-width: 750px) {
  .App {
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
    height: auto;
  }

  .compare-wrapper {
    width: 100vw;
    height: 100vh;
  }

  .map-wrapper {
    height: 87.4vh;
  }
}

.map-wrapper {
  width: 100%;
  max-height: 100vh;
  height: 100vh;
}

@media only screen and (max-width: 750px) {

  .map-wrapper {
    height: 87.4vh;
  }
}

header {
  font-size: 32px;
}

.compare-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;

  background-color: #000;
  color: #fff;
  z-index: 2;
  width: 44%;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}

.compare {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex: 1 1;
          flex: 1 1;

  padding: 25px;
  overflow-y: auto;
}

.table {
  padding-top: 20px;
  padding-bottom: 20px;
}

.row {
  display: -webkit-flex;
  display: flex;
}

.row+.row {
  border-top: 1px solid white;
  padding-top: 20px;
  margin-top: 20px;
}

.entry {
  width: 50%;
}

footer {
  margin-top: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  font-weight: normal;
  font-size: 12px;
}

.footer-left {
  margin-right: auto;

}

@media only screen and (max-width: 750px) {
  
  .compare-wrapper {
    width: 100vw;
    height: 100vh;
  }
}

